*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html {
  font-family: "PT Mono", "Arial", sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  position: relative;
  margin: 0 auto;
  min-height: 100vh;
  min-width: 320px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column; }

body.block {
  overflow: hidden; }

body.block::before {
  content: "";
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: #000000;
  opacity: 0.5; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.list-unstyled {
  list-style: none;
  padding: 0;
  margin: 0; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

.content-wrapper {
  min-width: 308px;
  max-width: 1432px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 9px;
  padding-right: 12px; }

.button,
.site-form button {
  display: inline-block;
  min-width: 180px;
  min-height: 45px;
  padding: 13px 15px;
  border-radius: 5px;
  border: 1px solid #ffffff;
  background-color: transparent;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  text-align: center;
  vertical-align: middle;
  font-weight: 600;
  color: #ffffff;
  text-transform: uppercase;
  cursor: pointer; }
  .button:hover,
  .site-form button:hover {
    background-color: #00b2ff; }
  .button:active,
  .site-form button:active {
    opacity: 0.8; }

.section-title {
  margin-top: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400; }

@media (max-width: 1023px) {
  .content-wrapper {
    padding-left: 25px;
    padding-right: 25px; }
  .section-title {
    font-size: 32px;
    line-height: 45px; } }

@media (max-width: 767px) {
  .content-wrapper {
    padding-left: 16px;
    padding-right: 16px; }
  .section-title {
    font-size: 28px;
    line-height: 39px; } }
